const formsSend = () => {
    const inputNum = document.querySelectorAll('.text-field__input')[2]
    const inputEmail = document.querySelectorAll('.text-field__input')[1]
    const inputName = document.querySelectorAll('.text-field__input')[0]
    const inputMessage = document.querySelector('.message')
    const successFiled = document.querySelector('.success')
    const formElements = [inputEmail, inputMessage, inputName, inputNum]
    const form = document.querySelector('form')

    const inputOptions = {
        number(element) {
            let value = element.value
            value = value.replace(/\D/g, '');
            value = value.replace(/(\d{2})(\d)/, '($1) $2');
            value = value.replace(/(\d{1})(\d{4})(\d{0,4})/, '$1 $2-$3');

            element.value = value.slice(0, 16)
        },
        email(element) {
            authentic(element)
        },
        name(element) {
            element.classList.remove('text-field__input-is-error')
        },
        message(element) {
            element.classList.remove('text-field__input-is-error')
        }
    }

    const handleInput = (element) => {
        const currentFunction = inputOptions[element.classList[1]]

        element.addEventListener('focus', () => {
            element.parentElement.classList.add('text-field__input--empty')
        })

        element.addEventListener('blur', () => {
            if (!element.value) {
                element.parentElement.classList.remove('text-field__input--empty')
            }
        })

        element.addEventListener('keyup', () => {
            currentFunction(element)
        })
    }

    formElements.forEach(element => {
        handleInput(element)
    })

    const authentic = (element) => {
        const emailPattern = /^[a-zA-Z0-9._-]+@[a-zA-Z0-9.-]{2,}\.[a-zA-Z]{3,}$/
        const emailIsValid = emailPattern.test(element.value)
        const nameIsValid = inputName.value.length === 0
        const numberIsValid = inputNum.value.length === 16 || inputNum.value.length === 0
        const messageIsValid = inputMessage.value.length === 0

        if (!emailIsValid && element.value.length !== 0) {
            element.classList.add('text-field__input-is-error')
        } else {
            element.classList.remove('text-field__input-is-error')
        }

        return [emailIsValid, nameIsValid, numberIsValid, messageIsValid]
    }

    async function getCsrfToken() {
        const response = await fetch('https://backend-cidadeclipse.vercel.app/csrf-token')
        const data = await response.json()
        return data.csrfToken
    }

    form.addEventListener('submit', async (event) => {
        event.preventDefault()
        const csrfToken = await getCsrfToken()
        const data = {
            emailUser: inputEmail.value,
            text: inputMessage.value,
            number: inputNum.value.replace(/[^\d]/g, ''),
            name: inputName.value
        }

        const [emailIsValid, nameIsValid, numberIsValid, messageIsValid] = authentic(inputEmail)

        !emailIsValid ? inputEmail.classList.add('text-field__input-is-error')
            : ''
        nameIsValid ? inputName.classList.add('text-field__input-is-error') : ''
        messageIsValid ? inputMessage.classList.add('text-field__input-is-error') : ''

        return fetch('https://backend-cidadeclipse.vercel.app/send', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'CSRF-Token': csrfToken
            },
            body: JSON.stringify(data)
        }).then(response => response.json())
            .then(data => {
                formElements.forEach(element => {
                    element.value = ''
                    element.parentElement.classList.remove('text-field__input--empty')
                })
                successFiled.classList.add('success-is-active')
                setTimeout(() => {
                    successFiled.classList.remove('success-is-active')
                }, 3000)
            }).catch(error => {
                console.error(error)
            })
    })
}

export default formsSend
