const copyPaste = () => {
    const inputPix = document.querySelector('.input-pix')
    const button = document.querySelector('.button-pix')
    
    button.addEventListener('click', (e) => {
        e.preventDefault()
        inputPix.classList.add('input-pix--is-copied')
        navigator.clipboard.writeText(inputPix.value).then(() => {
        })

        setTimeout(() => {
            inputPix.classList.remove('input-pix--is-copied')
            
        }, 2000)
    })
}

export default copyPaste
